import React, { useState } from "react";
import { Col, Row, Tab, Nav } from "react-bootstrap";
import "react-datepicker/dist/react-datepicker.css";
import AllPresForms from "./AllPresForms";
import PendingPresForms from "./PendingPresForms";
import CompletedPresForms from "./CompletedPresForms";
import RejectedPresForms from "./RejectedPresForms";

export default function PrescriptionStatus() {
  const [key, setKey] = useState("all");

  return (
    <div className="usermanagement-mainclass my-3">
      <Tab.Container
        id="left-tabs-example"
        defaultActiveKey="all"
        className="Admin-Tabs-SubMain"
        onSelect={(k) => setKey(k)}
      >
        <Row>
          <div className="mb-2">
            <Nav
              variant="pills"
              className="Nav-MainAdmin doctor-prescription-tabs"
            >
              <Nav.Item>
                <Nav.Link
                  eventKey="all"
                  style={{ background: "white", color: "#B3B3B3" }}
                  className="Admin-Tabs-All"
                >
                  All
                </Nav.Link>
              </Nav.Item>

              <Nav.Item>
                <Nav.Link
                  eventKey="pending"
                  style={{ background: "white", color: "#B3B3B3" }}
                  className="Admin-Tabs-All"
                >
                  Pending
                </Nav.Link>
              </Nav.Item>

              <Nav.Item>
                <Nav.Link
                  eventKey="completed"
                  style={{ background: "white", color: "#B3B3B3" }}
                  className="Admin-Tabs-All"
                >
                  Completed
                </Nav.Link>
              </Nav.Item>

              <Nav.Item>
                <Nav.Link
                  eventKey="rejected"
                  style={{ background: "white", color: "#B3B3B3" }}
                  className="Admin-Tabs-All"
                >
                  Rejected
                </Nav.Link>
              </Nav.Item>
            </Nav>
          </div>

          <Col lg={12} md={12}>
            <Tab.Content>
              <Tab.Pane eventKey="all">
                {key === "all" && <AllPresForms />}
              </Tab.Pane>
              <Tab.Pane eventKey="pending">
                {key === "pending" && <PendingPresForms />}
              </Tab.Pane>

              <Tab.Pane eventKey="completed">
                {key === "completed" && <CompletedPresForms />}
              </Tab.Pane>

              <Tab.Pane eventKey="rejected">
                {key === "rejected" && <RejectedPresForms />}
              </Tab.Pane>
            </Tab.Content>
          </Col>
        </Row>
      </Tab.Container>
    </div>
  );
}
