import React from "react";
import { Card } from "react-bootstrap";
import PrescriptionStatus from "./PrescriptionStatus";

const CustomCard = ({ value, title }) => (
  <Card className="h-100">
    <Card.Body className="d-flex align-items-center justify-content-center flex-column">
      <h3 className="fw-bold">{value}</h3>
      <Card.Title className="m-0">{title}</Card.Title>
    </Card.Body>
  </Card>
);

function OnlinePrescriptionDashboard() {
  const cardData = [
    { value: "12", title: "All" },
    { value: "09", title: "Pending" },
    { value: "03", title: "Completed" },
    { value: "08", title: "Rejected" },
  ];

  return (
    <div>
      <div className="doctor-prescription-dashbaord">
        {cardData.map((data, index) => (
          <CustomCard key={index} {...data} />
        ))}
      </div>
      <div>
        <PrescriptionStatus />
      </div>
    </div>
  );
}

export default OnlinePrescriptionDashboard;
